<template>
  <div class="businessProcess-top-style">
    <div class="top-main border-style">
      <div class="title">客户满意率</div>
      <div
          class="charts-main"
          v-loading="!customerSatisfactionRes"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
      >
        <bda-line-charts
            v-if="customerSatisfactionRes"
            :chartData="customerSatisfactionLineData"
            :yDw="dw"
            :showLegend="false"
        ></bda-line-charts>
      </div>
    </div>
<!--    <div class="top-main border-style">-->
<!--      <div class="title">{{labelTit}}业务量</div>-->
<!--      <div-->
<!--        class="charts-main"-->
<!--        v-loading="!monthLineRes"-->
<!--        element-loading-background="rgba(0, 0, 0, 0.2)"-->
<!--        element-loading-text="加载中"-->
<!--      >-->
<!--        <bda-line-charts-->
<!--          v-if="monthLineRes"-->
<!--          :chartData="monthLineData"-->
<!--          :nameXs="nameXs"-->
<!--        ></bda-line-charts>-->
<!--      </div>-->
<!--    </div>-->
<!--    <div class="top-main border-style">-->
<!--      <div class="title">业务类型占比</div>-->
<!--      <div-->
<!--        class="charts-main"-->
<!--        v-loading="!forTypeRes"-->
<!--        element-loading-background="rgba(0, 0, 0, 0.2)"-->
<!--        element-loading-text="加载中"-->
<!--      >-->
<!--        <bda-pie-charts-->
<!--          v-if="forTypeRes"-->
<!--          :pieChartsData="forTypeData"-->
<!--          :center="['47%', '55%']"-->
<!--          :radius="['15%', '60%']"-->
<!--          :minAngle="30"-->
<!--        ></bda-pie-charts>-->
<!--      </div>-->
<!--    </div>-->
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
import bdaLineCharts from '../../../widgets/bda-line-charts/bda-line-charts';
// import bdaPieCharts from '../../../widgets/bda-pie-charts/bda-pie-charts';
import qs from 'qs';
export default {
  name: "businessProcessTop",
  components: {
    bdaLineCharts,
    // bdaPieCharts
  },
  mixins: [Interface],
  data() {
    return {
      monthLineRes: false, //月业务量--刷新状态值
      monthLineData: {},   //月业务量数据
      forTypeRes: false,   // 总业务量占比--刷新状态值
      forTypeData: {},
      colors: ['#f3e28a', '#0150f9', '#df896d', '#02e0dc', '#8085e9', '#90ed7d'],
      nameXs: '日',
      labelTit: '月',

      customerSatisfactionLineData: {},    //客户满意率
      customerSatisfactionRes: false,
      dw: '%',
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    if (this.$parent.dateType=='1'){
      this.labelTit = '年'
    }else {
      this.labelTit = '月'
    }
    // 月业务量
    this.getBusinessForMonth(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      this.bussinessDataFilter(res);
    }).catch(() => {
      this.forTypeRes = true;
    });
    // 业务类型占比
    this.getBusinessForType(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //   window.console.log('res',res)
      this.businessForTypeFilter(res);
    }).catch(() => {
      this.monthLineRes = true;
    });

    this.getCustomerSatisfaction(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
        window.console.log('客户满意率', res);
      this.customerSatisfactionData(res);
    }).catch(() => {
      this.customerSatisfactionRes = true;
    });
  },
  methods: {
    customerSatisfactionData(data) {
      let xData = [], dataInfo = [];
      if (data) {
        for (let item of data) {
          xData.push(item.dates);
          dataInfo.push(item.per);
        }
      }
      this.customerSatisfactionRes = false;
      this.customerSatisfactionLineData = {
        dimension: xData,
        dataMap: { '客户满意度': dataInfo },
        color: ['#2ed89a']
      };
      window.console.log(this.customerSatisfactionLineData,'7777777')
      this.$nextTick(() => {
        this.customerSatisfactionRes = true;
      })
    },
    bussinessDataFilter(data) {
      let xData = [], dataMap = { '上月': [], '本月': [] };
      let status = '';
      if (data) {
        let beforMonthLen = data.beforeMonth ? data.beforeMonth.length : 0;
        let presentMonthLen = data.presentMonth ? data.presentMonth.length : 0;
        // 判读哪个数据长度较长，则作为x轴的维度信息
        if (beforMonthLen - presentMonthLen >= 0) {
          status = 'before'
        } else {
          status = 'present'
        }
        if (beforMonthLen > 0) {
          for (let item of data.beforeMonth) {
            if (item.handledate) {
              let disLength = item.handledate.length;
              let name = item.handledate.substring(disLength - 2, disLength);
              item.value = item.cc;
              item.name = name;
              if (status === 'before') {
                xData.push(name);
              }
            }
          }
        }
        if (presentMonthLen > 0) {
          for (let item of data.presentMonth) {
            if (item.handledate) {
              let disLength = item.handledate.length;
              let name = item.handledate.substring(disLength - 2, disLength);
              item.value = item.cc;
              item.name = name;
              if (status === 'present') {
                xData.push(name);
              }
            }
          }
        }
      }
      dataMap = {
        '本月': data.presentMonth,
        '上月': data.beforeMonth
      };
      this.monthLineRes = false;
      this.monthLineData = {
        dimension: xData,
        dataMap: dataMap,
        color: ['#019eff', '#00FFD4']
      };
      this.$nextTick(() => {
        this.monthLineRes = true;
      })
    },
    businessForTypeFilter(data) {
      let _data = [];
      if (data) {
        for (let item of data) {
          item.name = item.busisubtype ? item.busisubtype : ''
          item.value = (item.ccsum !== (undefined || null)) ? item.ccsum : 0;
          item.y = item.value;
          _data.push(item);
        }
      }
      this.forTypeRes = false;
      // this.forTypePie.data=_data;
      window.console.log(_data, '_data');
      this.forTypeData = {
        dataMap: _data,
        color: [['#97FF61', '#5FDA76'], ['#B0FF95', '#5FDA76'], ['#95FFC7', '#2DCFAB'], ['#95FFEC', '#2BC5D8'], ['#6AE9FF', '#00AAFF'],]
      };
      this.$nextTick(() => {
        this.forTypeRes = true;
      })
    }
  }
}
</script>

<style scoped lang="scss">
.businessProcess-top-style {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  .top-main {
    background: url("../../../assets/img/管理框1.png") no-repeat top center;
    background-size: 104% 100%;
    // padding: 10px 20px;
    // box-sizing: border-box;
    flex: 1;
    height: 100%;
    position: relative;
    float: left;
    font-size: 16px;
    .title {
      // background: linear-gradient(
      //   to bottom,
      //   rgba(15, 146, 229, 0.3),
      //   #061b3a
      // );
      color: #00e7ff;
      // line-height: 38px;
      height: 22%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .charts-main {
      height: calc(100% - 22% - 40px);
      // box-sizing: border-box;
      width: calc(100% - 40px);
      position: relative;
      padding: 0 20px;
    }
  }
}
</style>